body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    background-color: rgb(29, 29, 29);
    color: white;
  }
  
  .flip-container {
    perspective: 1000px; 
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  
  
  .flipper {
    position: absolute;
    width: 63.5mm;
    height: 88mm;
    transform-style: preserve-3d;
    transition: transform 0.6s ease-in-out; 
  }
  
  
  
  .front, .back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden; 
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
    color: white;
    background-size: 100% 100% ;
    border-radius: 16px;
  }
  
  .front {
    background-color: #3498db; 
    background-image: url('./cardsimg/back.png');
  }
  
  .back {
    background-color: #e74c3c; 
    transform: rotateY(180deg); 
    
  }
.penis{
  color: white;
  border-radius: 25px;
  font-size: large;
  width: 150px;
  height: 75px;
 
}
#buttons{
  display: none;
