button{
    height: 256px;
    width: 467px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background-color:rgb(22, 34, 73);
    box-shadow: none;
    margin: none;
    padding: none;
    border: rgb(29, 29, 29) solid 3px;
}
#jajca {
    text-align: center;
    display: flex; 
    flex-wrap: wrap; 
    justify-content: center; 
    align-items: center; 
    height: 100vh; 
    color: white;
}
@media screen and (max-width: 600px) {
    button{
        height: 128px;
        width: 233px;
    }
}